.nav {
  &__list {
    display: flex;
    list-style: none;
  }

  &__item:not(:last-child) {
    margin-right: 20px;
  }

  &__link {
    display: flex;
    align-items: center;
    text-decoration: none;
    padding: 23px 0;
    font-size: 12px;
    text-transform: uppercase;
    color: $dark-blue-color;
    transition: color 0.3s ease-in-out;

    &:hover {
      color: $aqua-color;
    }
  }
}
